<template>
  <div class="TakeOutFood">
    <van-search class="search"
      shape="round"
      show-action
      placeholder="搜你所想，享你所爱"
      @focus="seach"
    >
      <template #left>
        <div class="van-right" @click="pushMy">我的</div>
      </template>
      <template #action>
        <div class="van-left" @click="pushAddress">登录</div>
      </template>
    </van-search>
    <div class="swip">
      <van-swipe
        @change="onChange"
        indicator-color="#ffffff"
        autoplay="3000"
        style="height: 163.03px;"
      >
        <van-swipe-item v-for="(item, index) in bannerData" :key="index">
          <img class="auto-img" v-lazy="item.bannerImg" alt />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/4</div>
        </template>
      </van-swipe>
      <div class="swip-txt"><span>经典现磨咖啡，让你的生活飘香四溢</span></div>
    </div>


    <div class="menu-content">
      <div class="aside-menu fl">
        <div class="item" :ref="'item' + index" :class="{active: item.isActive}" v-for="(item, index) in typeData" :key="index" @click="toogle(item, index)">{{item.typeDesc}}</div>
        <div class="line" :style="{left: left + 'px'}"></div>
      </div>
      <div class="content fl clearfix">
        <div class="fl content-item" @click="pushdetail(item.pid)" :class="[index % 2 == 0 ? 'left' : 'right']" v-for="(item, index) in goodsData" :key="index">
          <div>
            <img
              class="auto-img"
              v-lazy="item.smallImg"
              alt
            />
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="pro-enname one-text">{{item.enname}}</div>
          <div class="pro-price">￥{{item.price}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/TakeOutFood.less"
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");

export default {
  data() {
    return {
      bannerData: [],
      current: 0,
      typeData: [],
      left: 0,
      goodsData: []
    };
  },
  created() {
    this.getBanner();
    this.Mood();
    this.getData({type: 'isHot'});
    this.goodfig();
  },

  computed: {
    ...mapState(["bannerImg", "sides", "goods", "width"])
  },
  methods: {
    pushAddress(){
        this.$router.push('/loGin')
    },
    pushMy(){
        this.$router.push('/home/my')
    },
    onChange(index) {
      this.current = index;
    },

    ...mapMutations(['changegoods', 'goodsreo']),

    goodfig() {
      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.left = i * this.width;
          return;

        }
      }
      
    },

    seach(){
      this.$router.push('/serch')
    },

    getBanner() {
      if (this.bannerImg.length > 0) {
        this.bannerData = this.bannerImg;
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          this.$toast.clear();
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
            this.changegoods({
              key: "bannerImg",
              value: result.data.result
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    Mood() {

      if (this.sides.length > 0) {
        this.typeData = this.sides;
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: 'GET',
        url: '/type',//商品类型
        params: {
          appkey: this.appkey
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 400) {
          result.data.result.forEach(v => {
            v.isActive = false;
          })
          result.data.result.unshift({type: 'isHot', typeDesc: '最新推荐', isActive: true});
          this.typeData = result.data.result;

          this.$nextTick(() => {
            let firstItemWidth = this.$refs.item0[0].clientWidth;
            this.changegoods({
              key: 'width',
              value: firstItemWidth
            })
          })

          this.changegoods({
            key: 'sides',
            value: result.data.result
          })

        }
        
      }).catch(err => {
        this.$toast.clear();
      })
    },

    toogle(item, index) {
      if (item.isActive) {
        return;
      }

      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.typeData[i].isActive = false;
          break;
        }
      }

      item.isActive = true;
      

      this.left = this.width * index;

      this.getData(item);

    },

    getData(item) {


      if (this.goods[item.type]) {
        this.goodsData = this.goods[item.type];
        return;
      }

      this.goodsData = [];

      let params = {
        appkey: this.appkey,
        key: 'type',
        value: item.type
      };

      if (item.type == 'isHot') {
        params.key = 'isHot';
        params.value = 1;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: 'GET',
        url: '/typeProducts', //详情
        params
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 500) {
          this.goodsData = result.data.result;
          
          this.goodsreo({
            type: item.type,
            value: result.data.result
          })
          
        }
      }).catch(err => {
        this.$toast.clear();
      })
      
    },

    pushdetail(pid) {
      this.$router.push({name: 'Detail', query: {pid}});
    }
  }
};
</script>
